  getFilteredData(data) {
    let filtered = [...data];
    if (this.filters.status !== 'all') {
      filtered = filtered.filter(item => item.status === this.filters.status);
    }
    if (this.filters.type !== 'all') {
      filtered = filtered.filter(item => item.type === this.filters.type);
    }
    if (this.filters.search) {
      const search = this.filters.search.toLowerCase();
      filtered = filtered.filter(
        item =>
          (item.name && item.name.toLowerCase().includes(search)) ||
          (item.title && item.title.toLowerCase().includes(search)) ||
          (item.partnerName && item.partnerName.toLowerCase().includes(search))
      );
    }
    return filtered;
  }

  async loadData() {
    if (!this.useAPI) {
      this.loadFromLocalStorage();
      return;
    }

    try {
      if (!this.connectionManager.isFullyConnected()) {
        this.loadFromLocalStorage();
        return;
      }

      const [partnerships, partners, collaborations, projects, agreements, events, analytics] =
        await Promise.all([
          this.apiClient.get(
            this.API_ENDPOINTS.partnerships || '/api/advanced-partnerships/partnerships'
          ),
          this.apiClient.get(this.API_ENDPOINTS.partners || '/api/advanced-partnerships/partners'),
          this.apiClient.get(
            this.API_ENDPOINTS.collaborations || '/api/advanced-partnerships/collaborations'
          ),
          this.apiClient.get(this.API_ENDPOINTS.projects || '/api/advanced-partnerships/projects'),
          this.apiClient.get(
            this.API_ENDPOINTS.agreements || '/api/advanced-partnerships/agreements'
          ),
          this.apiClient.get(this.API_ENDPOINTS.events || '/api/advanced-partnerships/events'),
          this.apiClient.get(
            this.API_ENDPOINTS.analytics || '/api/advanced-partnerships/analytics'
          ),
        ]);

      this.partnerships = partnerships.data || [];
      this.partners = partners.data || [];
      this.collaborations = collaborations.data || [];
      this.projects = projects.data || [];
      this.agreements = agreements.data || [];
      this.events = events.data || [];
      this.analytics = analytics.data || [];

      this.saveToLocalStorage();
      this.updateContent();
    } catch (error) {
      console.error('Error loading partnerships data:', error);
      this.loadFromLocalStorage();
    }
  }

  setupRealtimeSync() {
    if (!this.realtimeSync) return;
    this.realtimeSync.subscribe('advanced-partnerships', '*', update => {
      if (update.action === 'create' || update.action === 'update' || update.action === 'delete') {
        this.loadData();
      }
    });
  }

  setupConnectionMonitoring() {
    if (!this.connectionManager) return;
    this.connectionManager.on('online', () => {
      this.loadData();
    });
  }

  switchView(view) {
    this.currentView = view;
    this.updateContent();
  }

  handleFilterChange(type, event) {
    this.filters[type] = event.target.value;
    this.updateContent();
  }

  handleSearch(event) {
    this.filters.search = event.target.value;
    this.updateContent();
  }

  updateContent() {
    const content = document.getElementById('partnershipsContent');
    if (content) {
      content.innerHTML = this.renderCurrentView();
    }
  }

  getStatusText(status) {
    const statusMap = {
      active: 'ŸÜÿ¥ÿ∑',
      pending: 'ŸÇŸäÿØ ÿßŸÑÿßŸÜÿ™ÿ∏ÿßÿ±',
      completed: 'ŸÖŸÉÿ™ŸÖŸÑ',
      suspended: 'ŸÖÿπŸÑŸÇ',
      signed: 'ŸÖŸàŸÇŸëÿπ',
      expired: 'ŸÖŸÜÿ™ŸáŸä',
    };
    return statusMap[status] || status;
  }

  getTypeText(type) {
    const typeMap = {
      strategic: 'ÿßÿ≥ÿ™ÿ±ÿßÿ™Ÿäÿ¨Ÿä',
      educational: 'ÿ™ÿπŸÑŸäŸÖŸä',
      medical: 'ÿ∑ÿ®Ÿä',
      financial: 'ŸÖÿßŸÑŸä',
      research: 'ÿ®ÿ≠ÿ´Ÿä',
    };
    return typeMap[type] || type;
  }

  formatDate(date) {
    if (!date) return 'ÿ∫Ÿäÿ± ŸÖÿ≠ÿØÿØ';
    return new Date(date).toLocaleDateString('ar-SA');
  }

  formatCurrency(amount) {
    if (!amount) return '0 ÿ±.ÿ≥';
    return `${amount.toLocaleString('ar-SA')} ÿ±.ÿ≥`;
  }

  saveToLocalStorage() {
    try {
      localStorage.setItem('advancedPartnerships', JSON.stringify(this.partnerships));
      localStorage.setItem('advancedPartners', JSON.stringify(this.partners));
      localStorage.setItem('advancedCollaborations', JSON.stringify(this.collaborations));
      localStorage.setItem('advancedProjects', JSON.stringify(this.projects));
      localStorage.setItem('advancedAgreements', JSON.stringify(this.agreements));
      localStorage.setItem('advancedEvents', JSON.stringify(this.events));
      localStorage.setItem('advancedAnalytics', JSON.stringify(this.analytics));
    } catch (error) {
      console.error('Error saving to localStorage:', error);
    }
  }

  loadFromLocalStorage() {
    try {
      this.partnerships = JSON.parse(localStorage.getItem('advancedPartnerships') || '[]');
      this.partners = JSON.parse(localStorage.getItem('advancedPartners') || '[]');
      this.collaborations = JSON.parse(localStorage.getItem('advancedCollaborations') || '[]');
      this.projects = JSON.parse(localStorage.getItem('advancedProjects') || '[]');
      this.agreements = JSON.parse(localStorage.getItem('advancedAgreements') || '[]');
      this.events = JSON.parse(localStorage.getItem('advancedEvents') || '[]');
      this.analytics = JSON.parse(localStorage.getItem('advancedAnalytics') || '[]');
    } catch (error) {
      console.error('Error loading from localStorage:', error);
    }
  }

  setupEventListeners() {
    this.createPartnership = this.createPartnership.bind(this);
    this.createPartner = this.createPartner.bind(this);
    this.switchView = this.switchView.bind(this);
    this.handleFilterChange = this.handleFilterChange.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.viewPartnership = this.viewPartnership.bind(this);
    this.editPartnership = this.editPartnership.bind(this);
  }

